
.re_nav_fixed {
  width: 100%;
  overflow: hidden;
  z-index: 1111;
  position: fixed;
  left: 0;
  top: 0;
}
.re_search {
  background: #1ebf98;
  line-height: 0;
  padding: .2rem;
  position: relative;
}
.re_search input[type="search"] {
  display: inline-block;
  height: .9rem;
  width: 8rem;
  outline: none;
  border: none;
  border-radius: .45rem;
  background: #f2f2f2;
  box-sizing: border-box;
  padding: 0 0.9rem;
  font-size: .4rem;
  vertical-align: top;
}
.re_search img {
  width: 0.58rem;
  height: 0.58rem;
  position: absolute;
  left: 0.42rem;
  top: 50%;
  margin-top: -0.25rem;
}
.re_search span {
  display: inline-block;
  width: 1rem;
  height: 0.9rem;
  font-size: 0.42rem;
  vertical-align: top;
  line-height: 0.9rem;
  text-align: center;
  color: #fff;
}
.business_content {
  /*padding-bottom:3rem;*/
  box-sizing: border-box;
  position: relative;
  height: 100%;
  overflow: hidden;
  margin-top: 1.27rem;
}
.business_content ul {
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
  height: 100%;
}
.business_content ul.business_left {
  width: 2.5rem;
  float: left;
}
.business_content ul.business_left li {
  padding: .45rem .3rem;
  font-size: .4rem;
  border-bottom: 1px solid #ccc;
  position: relative;
}
.business_content ul.business_left li .left_red {
  background: #ff461d;
  color: #fff;
  position: absolute;
  right: 0.08rem;
  top: 0.08rem;
  font-size: .3rem;
  min-width: .4rem;
  text-align: center;
  border-radius: .2rem;
  padding: .02rem .05rem;
}
.business_content ul.business_left li.active_ia {
  background: #fff;
}
.business_content ul.business_left li.active_ia:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: .1rem;
  height: 100%;
  background: #1ebf98;
}
.business_content ul.business_right {
  margin-left: 2.5rem;
  background: #fff;
}
.business_content ul.business_right li .type_title {
  padding: .2rem;
  box-sizing: border-box;
  width: 100%;
  background: #f1f1f1;
  line-height: .6rem;
}
.business_content ul.business_right li .type_title strong {
  font-size: .45rem;
  color: #666;
}
.business_content ul.business_right li .type_title span {
  font-size: .3rem;
  color: #999;
}
.business_content ul.business_right li .single_commodity {
  background: #fff;
  padding: .3rem;
  border-bottom: 1px solid #ccc;
}
.business_content ul.business_right li .single_commodity .single_l {
  width: 1.5rem;
  height: 1.5rem;
  float: left;
}
.business_content ul.business_right li .single_commodity .single_l img {
  width: 100%;
}
.business_content ul.business_right li .single_commodity .single_r {
  margin-left: 1.7rem;
}
.business_content ul.business_right li .single_commodity .single_r h4 {
  font-size: .5rem;
  color: #333;
}
.business_content ul.business_right li .single_commodity .single_r h4 em {
  font-style: normal;
  color: #666;
  font-size: 14px;
}
.business_content ul.business_right li .single_commodity .single_r p {
  font-size: 20px;
  color: #fe5579;
  margin-top: 16px;
}
.business_content ul.business_right li .single_commodity .single_r .xiaolq {
  font-size: .4rem;
  color: #666;
}
.business_content ul.business_right li .single_commodity .single_r .add_remove {
  margin-top: .2rem;
  height: 1rem;
  line-height: .5rem;
  font-size: .4rem;
}
.business_content ul.business_right li .single_commodity .single_r .add_remove span {
  font-size: .45rem;
  font-weight: 0;
  color: #f60;
  vertical-align: middle;
}
.business_content ul.business_right li .single_commodity .single_r .add_remove .add_remove_btn {
  float: right;
}
.business_content ul.business_right li .single_commodity .single_r .add_remove .add_remove_btn .commodity_count {
  color: #666;
  margin: 0 .15rem;
}
.business_content ul.business_right li .single_commodity .single_r .add_remove .add_remove_btn img {
  width: 0.7rem;
  height: 0.7rem;
  vertical-align: middle;
}
.push_dingdan {
  padding-bottom: 2rem;
  width: 100%;
  height: 2.4rem;
  /*overflow: hidden;*/
  vertical-align: middle;
  line-height: 2.4rem;
  /*position: fixed;
  	background: #fff;
  	left: 0;
  	bottom: 150px;*/
}
.push_dingdan span {
  display: inline-block;
  font-size: 28px;
  color: #666;
  vertical-align: middle;
}
.push_dingdan span i,
.push_dingdan span em {
  font-style: normal;
}
.push_dingdan span em {
  color: #fe5579;
}
.push_dingdan .total_num {
  padding: 0 24px;
}
.push_dingdan .push_houtai {
  float: right;
  width: 4.14rem;
  height: 1.2rem;
  background: #1ebf98;
  color: #fff;
  text-align: center;
  line-height: 1.2rem;
  border-radius: 0.6rem;
  margin-right: 24px;
  margin-top: 55px;
}



